.house.loaded {

  header {
    
    .jumbo {
      height:340px;
      position:relative;
      background-size:cover;

      .jumbo-contents {
        position:absolute;
        left:50px;
        bottom:30px;
        line-height: 2.5em;
        
        * {
          color:#fff;
          margin:8px 0 0 0;  
          padding:0;
          text-shadow:0 0 6px rgba(0,0,0,.9);
          font-family: 'Slabo 27px', serif;
        }

        h1 {
          font-size:40px;
        }

        h2 {
          font-size:30px;
          font-weight:normal;
        }
        
        .house-properties {
          font-size:26px;
        }
        
      }

    }

    .mini-map {
      position:absolute;
      top:225px;
      right:80px;

      // Cf. https://github.com/Leaflet/Leaflet/issues/3575#issuecomment-688644225
      .leaflet-tile-container img {
        width: 256.5px !important;
        height: 256.5px !important;
      }

      .leaflet-container {
        width:360px;
        height:270px;
        border-radius:12px;
        border:5px solid #fff;
        overflow:hidden;  
      }
    }
  }

  main {
    
    .section {
      padding:50px 50px 0 50px;
      font-size:17px;
      font-family: 'Nunito Sans', serif;

      h3 {
        font-size:22px;
        color:#cb5526;
      }
    }

    .section.key-properties {
      padding-top:30px;

      ul, li {
        padding:0;
        margin:0;
        list-style-type:none;
      }

      li {
        padding:2px 0;
      }

      .anticon {
        font-size:20px;
      }

      .anticon, label {
        padding-right:5px;
        color:#cb5526;
      }

    }

    .section.atrium {

      p {
        display:inline;
      }

    }

    .section.images, .section.plans {

      .ant-image {
        margin-right:20px;
        border-radius:3px;
        overflow:hidden;
      }

    }

    .section.bibliography {

      p {
        font-family:'Times New Roman', Times, serif;
        font-size:18px;
        white-space: pre;
      }

    }

    .section.cite-as {
      
      p {
        position:relative;
        background-color:#f0f4f9;
        padding:20px 60px 20px 20px;
        border-radius:6px;
        font-family:'Times New Roman', Times, serif;
        font-size:18px;

        a {
          color:#545454;
          text-decoration:underline;
        }

        .anticon.copy {
          position:absolute;
          top:22px;
          right:22px;
          font-size:22px;
          color:#adadad;
        }

        .anticon.copy:hover {
          color:#5f5f5f;
        }
      }

    }

    .section:last-child {
      padding-bottom:80px;
    }
  
  }

}